import { Col, Row, Select } from "antd";
import IntlMessage from "components/util-components/IntlMessage";
import React, { useState } from "react";
import Chart from "react-apexcharts";
import { PieData, dataByName } from "./PieData";

const StatusPieChart = () => {
  const [filterData, setFilterData] = useState(PieData.series[0].name.props.id);
  const [pieData] = useState(PieData);

  const { Option } = Select;

  const onChange = value => {
    setFilterData(value);
  };

  const onSearch = value => {
    setFilterData(value);
  };
  return (
    <>
      <Row gutter={16} type="flex">
        <Col sm={24} md={24} lg={6}>
          <div className="mb-2">
            <Select
              showSearch
              style={{ width: "100%" }}
              placeholder={<IntlMessage id="component.dashboard.countrydropdown.title" />}
              optionFilterProp="children"
              onChange={onChange}
              onSearch={onSearch}
              filterOption={(value, option) => option.children.toLowerCase().includes(value.toLowerCase())}
            >
              {pieData.series.map(({ name }) => (
                <Option key={name.props.id} value={name.props.id}>
                  {name}
                </Option>
              ))}
            </Select>
          </div>
        </Col>
      </Row>
      <Row gutter={16} type="flex">
        <Col sm={24} md={24} lg={12}>
          <div className="code-box">
            <Chart
              options={pieData.options}
              series={dataByName.has(filterData) ? dataByName.get(filterData) : []}
              type="pie"
              height={300}
            />
          </div>
        </Col>
      </Row>
    </>
  );
};

export default StatusPieChart;
